/* Skill */
.skill {
  padding-left: 5%;
  padding-right: 5%;
  /* min-height: 500px; */
  padding-bottom: 5%;
  background-color: #dddddd;
}
.container {
  /* background-color: yellow; */
  width: 100%;
  display: flex;
  margin: 10px 0px 40px 0px;
}

.centered5 {
  display: flex;
  flex-direction: column;
  position: absolute;
  text-align: center;
  margin: auto;

  top: 0%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding-top: 60px;
}

.container2 {
  margin: 10px 0px 40px 0px;
  /* background-color: gray; */
  width: 100%;
  display: flex;
}
.container3 {
  margin: 10px 0px 40px 0px;
  width: 100%;
  display: flex;
}
.flex2 {
  /* padding-top: 30%; */
  /* margin: auto; */
  /* width: 100%; */
}
.center {
  padding-top: 100px;
  text-align: center;
  z-index: 5;
}
.center3 {
  padding-top: 100px;
  text-align: center;
  z-index: 5;
  font-weight: 900;
}
.center2 {
  padding-top: 400px;
  /* position: absolute; */
  z-index: 5;
}
.height2 {
  height: 130px;
  width: 130px;
  padding: 0px 0px 10px 0px;
}
.skilllabel {
  font-size: 30px;
  text-align: center;
  font-weight: 300;
}
.height3 {
  height: 130px;
  padding: 10px;
  transition: 0.2s;
}

.eachskill {
  width: 20%;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-content: center;
  /* justify-content: center; */
  align-items: center;
  /* align-self: center; */
}
.skills {
  width: 80%;
  padding: 20px;
  text-align: center;
  margin: auto;
  /* overflow: scroll; */
}
@media only screen and (max-width: 600px) {
  .eachskill {
    width: 50%;
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  .skills {
    width: 80%;
    padding: 0px;
    text-align: center;
    margin: auto;
    /* overflow: scroll; */
  }
  .height3 {
    height: 50%;
    width: 50%;
    padding: 10px;
    transition: 0.2s;
  }
  .skilllabel {
    font-size: 25px;
    text-align: center;
    font-weight: 300;
  }
}
