/* About */
@viewport {
  width: device-width;
  zoom: 1;
}
.about {
  /* height: 160vh; */
  background-color: #eeeeee;
  z-index: 5;
  /* position: relative; */
  /* align-content: center; */
  /* margin: 0 auto; */
  display: flex;
  flex-direction: column;
  padding-bottom: 30px;
}

.centered3 {
  display: flex;
  flex-direction: column;
  position: absolute;
  text-align: center;
  margin: auto;
  top: 18%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
}
.centeredd {
  position: absolute;
  text-align: center;
  margin: auto;
  left: 50%;
  transform: translate(-50%, 0%);
  margin-top: 265px;
}
.aboutpic {
  height: 616px;
  width: 450px;
  overflow: hidden;
}
.flex {
  display: flex;
  /* min-width: 100%; */
  /* margin-top: 50px; */
  /* margin: auto; */
  width: 100%;
}
.paper {
  position: relative;
  overflow: hidden;
  margin: auto;
  font-size: 20px;
  /* margin-left: 25px; */
}
.blackglass {
  /* REMEMBER TO SUBTRACT PAPER WIDTH - 2* PADDING for WIDTH & HEIGHT HERE! */
  height: 576px;
  width: 410px;
  padding: 20px;
  width: 95%;
  overflow-y: scroll;
  background: rgba(25, 25, 25, 0);
  position: absolute;
  color: rgb(255, 255, 255, 0);
  font-size: 19px;
  margin: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s ease;
}
.paper:hover .blackglass {
  /* width: 450px; */
  /* opacity: 0.8; */
  color: rgb(255, 255, 255, 1);
  background: rgba(25, 25, 25, 0.8);
}
.none4 {
  text-decoration: none;
  color: #0066cc;
  opacity: 0;
  transition: 0.3s ease;
}
.paper:hover .none4 {
  opacity: 1;
}

.who {
  text-align: center;
  font-weight: 300;
}
.p {
  font-weight: 300;
}

.quote {
  text-align: center;
  max-width: 100%;
  font-size: 25px;
  color: gray;
  font-style: italic;
  font-weight: 300;
  /* width: 1000px; */
  /* font-size: 25px;
  color: gray;
  font-style: italic; */
  /* font-weight: bold; */
}

.author {
  font-size: 25px;
  color: gray;
  padding: 10px;
  margin-bottom: 10%;
  font-weight: 300;
  font-style: italic;
}

.vertical2 {
  transition: 0.2s;
  opacity: 1;
}
.vertical2:hover {
  /* opacity: 0.7; */
  /* border: 3px solid black; */
  transform: scale(1.02);
  /* padding: 3px; */
}

.text2 {
  margin-top: 20px;
  font-size: 15px;
}
.aboutsplash {
  height: 120vh;
  /* width: 195.9vh; */
  opacity: 0.5;
  z-index: -1;
  text-align: center;
  position: absolute;
}

.education {
  padding: 20px;
}
.tjuva {
  width: 38%;
}
.avatar {
  vertical-align: middle;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.avatar2 {
  vertical-align: middle;
  width: 31px;
  height: 31px;
  border-radius: 50%;
}
.chip {
  padding: 10px 10px 10px 10px;
}
.timelineavatar2 {
  /* vertical-align: middle; */
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: 0.3s;
}
.timelineavatar2:hover {
  transform-origin: center center;
  transform: scale(1.4);
  top: 0%;
  left: 0%;
}
@media only screen and (max-width: 600px) {
  .flex {
    display: flex;
    flex-direction: column;
    /* min-width: 100%; */
    padding-top: 140px;
    margin: auto;
    width: 100%;
  }
  .about {
    /* margin-top: 700px; */
    background-color: #eeeeee;
    z-index: 5;
    position: relative;
    align-content: center;
    margin: 0 auto;
    display: flex;
  }
  .aboutpic {
    height: 616px;
    width: 350px;
    overflow: hidden;
  }
  .blackglass {
    /* REMEMBER TO SUBTRACT PAPER WIDTH - 2* PADDING for WIDTH & HEIGHT HERE! */
    height: 576px;
    width: 410px;
    padding: 20px;
    width: 95%;
    overflow-y: scroll;
    background: rgba(25, 25, 25, 0);
    position: absolute;
    color: rgb(255, 255, 255, 0);
    font-size: 19px;
    margin: auto;
    /* top: 50%; */
    /* left: 50%; */
    transform: translate(-50%, -50%);
    transition: 0.3s ease;
  }
}
